.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}

.nav_logo{
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav_logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;

}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}

.login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}

.login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border-radius: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.login-cart button:active{
    background: #f3f3f3;
}

.nav-cart-count{
    width: 22px;height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}

.nav-dropdown{
    display: none;
}

@media(max-width:1280px){
    .navbar{
        padding: 12px 50px;
    }
    .nav_logo img{
        width: 40px;
    }
    .nav_logo p{
        font-size: 25px;
    } 
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .login-cart{
        gap: 30px;
    }
    .login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
}

@media(max-width:1024px){
    .navbar{
        padding: 12px 30px;
    }

    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .login-cart img{
        width: 30px;
    }

    .nav-cart-count{
        width: 18px;
        height: 18px;
    }
}

@media(max-width:800px){
  .navbar{
    padding: 10px 0;
  }
  .nav-dropdown{
    display: block;
    width: 30px;
    rotate: -90deg; 
    transition: 0.5s;
    background-color: #fff;
  }

  .nav-menu{
    display: none;
    height: 80px;
    width: 100%;
    position: absolute;
    background-color: #fff;
    justify-content: center;
    top: 50px;
  }

  .nav-menu-visible{
    display: flex;
  }
  .nav-dropdown.open{
   transform: rotate(90deg);
  }
}

@media(max-width:500px){
    .navbar{
        padding: 8px 0px;
        gap: 0;
    }
    .nav_logo{
        transform: scale(0.8);
    }
    .nav-menu{
        height: 70px;
        top: 50px;
    }

    .login-cart{
        transform: scale(0.8);
    }
}