.hero{
    background: linear-gradient(180deg, #fed1ff, #efeaff22 60% );
    height: 100vh;
    display: flex;
}

.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    padding-left: 180px;
    line-height: 1.1;
}

.hero-left h2{
    font-size: 26px;
    font-weight: 600;
    color: #090909;
}

.hero-left p{
    font-size: 100px;
    font-weight: 700;
    color: #171717;
}

.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;
}

.hero-hand-icon img{
  width: 100px;    
}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}
.hero-latest-btn:hover{
    color: #090909;
    background: lightgreen;
}

.hero-right{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    
    }

    
/* responsive */

@media(max-width:1280px){
    
    
    .hero-left{
        padding-left: 100px;
      
    }

   .hero-left p{
    font-size: 60px;
   }
   .hero-left h2{
    font-size: 22px;
   }
   .hero-hand-icon img{
    width: 80px;
   }
   .hero-latest-btn{
    gap: 10px;
    width: 250px;
    height: 60px;
    margin-top: 20px;
    font-size: 18px;
   }

   .hero-right img{
    width: 500px;
   }

}

@media(max-width:1024px){
    .hero-left{
        padding-left:  80px;
    }

   .hero-left p{
    font-size: 50px;
   }
   .hero-left h2{
    font-size: 20px;
   }
   .hero-hand-icon img{
    width: 65px;
   }
   .hero-latest-btn{
   
    width: 220px;
    height: 50px;
    font-size: 16px;
   }

   .hero-right img{
    width: 470px;
   }

}

@media(max-width:800px){
    .hero-left{
        padding-left:30px;
        /* margin-top: -428px; */
    }

   .hero-left p{
    font-size: 40px;
   }
   .hero-left h2{
    font-size: 16px;
   }
   .hero-hand-icon img{
    width: 50px;
   }
   .hero-latest-btn{
  
    width: 175px;
    height: 40px;
    font-size: 13px;
   }

   .hero-right img{
    width: 300px;
   }

}

@media(max-width:500px){
    .hero{
       flex-direction: column;
    }

   .hero-left p{
    font-size: 50px;
   }
   .hero-left h2{
    font-size: 18px;
   }
   .hero-hand-icon img{
    width: 55px;
   }
   .hero-latest-btn{
     width: 200px;
     height: 45px;
     font-size: 14px;
   }

   .hero-right{
    /* display: none; */
   }

}