.shopBanner{
    display: block;
    margin:30px auto;
    width: 82%;
}

.indexSort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}

.sort{
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;

}

.sort p span{
    font-weight: 600;
}

.shopCategory-product{
    margin: 20px 170px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 80px;
}

.shopcategory-loadmore{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}

/* responsive */
@media(max-width:1280px){
   .shopBanner{
    margin: 20px auto;
    width: 90%;
   }
  .indexSort{
   width: 90%;
   margin: auto;
  }

   .sort{
    padding: 5px 10px;
    font-size: 12px;
   }

   .indexSort p{
    font-size: 12px;
   }

   .shopCategory-product{
    width: 90%;
    margin: 10px auto;
    row-gap: 40px;
   }

  .shopCategory-product .item{
    margin: auto;
  }
  .shopcategory-loadmore{
    margin: 100px auto;
    width: 200px;
    height: 60px;
    font-size: 16px;
  }

}

@media(max-width:800px){
    .shopBanner{
        width: 95%;
    }
    .shopCategory-product{
        width: 95%;
    }
    .shopcategory-loadmore{
        margin: 60px auto;
        font-size: 13px;
        width: 150px;
        height: 40px;
    }
}

@media(max-width:500px){
    .shopCategory-product{
        grid-template-columns: 1fr 1fr;
    }
    .shopBanner{
        width: 100%;
     
    }
}